@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/inter-medium.woff2") format("woff2"), url("../fonts/inter-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/inter-light.woff2") format("woff2"), url("../fonts/inter-light.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  font-family: "Inter", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
@media (max-width: 767px) {
  html {
    font-size: 14px;
    line-height: 120%;
  }
}

body {
  width: 100%;
  height: 100%;
  background-color: #D0D0D0;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}
input:-webkit-autofill:invalid {
  box-shadow: inset 0 0 0 1000px #ffffff;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.page-container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.centered {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px 0;
  min-height: 100vh;
}
@media (max-width: 767px) {
  .centered {
    padding: 16px 0;
  }
}

.container {
  width: 100%;
  max-width: 1448px;
  margin: 0 auto;
  padding: 0 24px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  margin: 0;
  padding: 2px;
  font-size: 16px;
  line-height: 120%;
  font-weight: 400;
  font-family: "Inter", "Arial", sans-serif;
  background-color: transparent;
  color: #000000;
  border: none;
  border-radius: 66px;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn:hover:not(.focus-visible), .btn.focus-visible:focus {
    color: #5A7FFF;
  }
}
.btn--blue {
  color: #5F9CF8;
  opacity: 1;
}
.btn--blue svg {
  stroke: #5F9CF8;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn--blue:hover:not(.focus-visible), .btn--blue.focus-visible:focus {
    opacity: 0.5;
  }
}
.btn--big {
  padding: 10px;
  border-radius: 5px;
  background-color: #FFE4E4;
}
.btn--opacity {
  opacity: 1;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn--opacity:hover:not(.focus-visible), .btn--opacity.focus-visible:focus {
    opacity: 0.5;
  }
}
.btn--create-list {
  padding: 16px 60px 16px 15px;
  background-color: #FFE4E4;
  border-radius: 15px 0 0 15px;
  position: relative;
  right: -40px;
}
@media (max-width: 1023px) {
  .btn--create-list {
    right: auto;
    border-radius: 15px;
    padding: 15px;
  }
}

.link {
  text-decoration: none;
  color: #5A7FFF;
  transition: all 0.3s ease;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .link:hover:not(.focus-visible), .link.focus-visible:focus {
    color: #000000;
  }
}

.todo-list {
  width: 100%;
  max-width: 1400px;
  background-color: #ffffff;
  padding: 40px;
  border-radius: 16px;
  overflow: hidden;
  margin: 20px 0;
}
@media (max-width: 767px) {
  .todo-list {
    padding: 16px;
  }
}
.todo-list__filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  padding-bottom: 35px;
}
@media (max-width: 1023px) {
  .todo-list__filter {
    align-items: flex-start;
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .todo-list__filter {
    gap: 15px;
  }
}
.todo-list__title {
  margin: 0;
  padding-bottom: 50px;
  font-weight: 300;
  line-height: 120%;
  font-size: 32px;
}
.todo-list__sub-title {
  margin: 0;
  display: flex;
  align-items: center;
  gap: 12px;
}
.todo-list__title-text {
  font-weight: 300;
  font-size: 20px;
  line-height: 120%;
}
.todo-list__info {
  display: flex;
  flex-direction: column;
}
.todo-list__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}
@media (max-width: 767px) {
  .todo-list__wrapper {
    gap: 15px;
    align-items: flex-start;
    flex-direction: column;
  }
}
.todo-list__items {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  margin: 0;
  padding-bottom: 24px;
}
.todo-list__check-list {
  padding: 0 20px 40px;
}
.todo-list__item {
  border-bottom: 1px solid #BEB5B5;
  max-height: 1000px;
  opacity: 1;
  overflow: hidden;
  visibility: visible;
  transition: visibility 0.3s ease, opacity 0.3s ease, max-height 0.3s ease;
}
.todo-list__item.is-hidden {
  max-height: 0;
  opacity: 0;
  visibility: hidden;
}
.todo-list__btn {
  font-weight: 300;
  font-size: 20px;
  line-height: 120%;
  color: #FF5A5A;
}
.todo-list__show-form .btn__subtext {
  display: none;
}
.todo-list__show-form.is-active .btn__subtext {
  display: inline;
}
.todo-list__show-form.is-active .btn__title {
  display: none;
}

.filter {
  display: inline-flex;
  gap: 40px;
}
@media (max-width: 767px) {
  .filter {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
}
.filter__type {
  padding: 2px 5px;
  background-color: transparent;
  position: relative;
  border: none;
  color: transparent;
  font-weight: 300;
  font-size: 20px;
  line-height: 120%;
}
.filter__type:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: transparent;
  transition: background-color 0.3s ease;
}
.filter__type:before {
  content: attr(data-before);
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #000000;
  transition: font-weight 0.3s ease, color 0.3s ease;
}
.filter__type .btn__title {
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .filter__type:hover:not(.focus-visible), .filter__type.focus-visible:focus {
    color: transparent;
  }
  .filter__type:hover:not(.focus-visible):before, .filter__type.focus-visible:focus:before {
    color: #5A7FFF;
  }
}
.filter__type.is-active:before {
  font-weight: 500;
}
.filter__type.is-active:after {
  background-color: #000000;
}

.task {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  position: relative;
  align-items: center;
  gap: 17px;
  padding: 20px 0;
}
@media (max-width: 767px) {
  .task {
    align-items: flex-start;
    flex-direction: column;
  }
}
.task__title, .task__text {
  margin: 0;
  position: relative;
  font-weight: 300;
  font-size: 20px;
  line-height: 120%;
  color: #000000;
}
.task__title {
  font-weight: 500;
}
.task__wrapper {
  display: flex;
  flex-direction: column;
  margin-right: auto;
}
.task__checkbox {
  display: block;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  cursor: pointer;
  border: 1px solid #5A7FFF;
  opacity: 1;
  transition: opacity 0.3s ease, background-color 0.3s ease;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .task__checkbox:hover:not(.focus-visible), .task__checkbox.focus-visible:focus {
    opacity: 0.5;
  }
}
[data-filter-status=finished] .task .task__checkbox {
  background-color: #5A7FFF;
}
[data-filter-status=finished] .task .task__title,
[data-filter-status=finished] .task .task__text {
  color: #979797;
}
[data-filter-status=finished] .task .task__title:after,
[data-filter-status=finished] .task .task__text:after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  top: calc(50% + 1px);
  left: 0;
  background-color: #979797;
}

.custom-input {
  position: relative;
  display: flex;
  flex-direction: column;
  width: auto;
  margin: 10px 0;
}
.custom-input label {
  display: flex;
  align-items: center;
  width: 100%;
}
.custom-input label input {
  width: 100%;
  flex-grow: 1;
  font-size: inherit;
  color: #000000;
  border-radius: 0;
  outline: none;
  transition: border-color 0.3s ease;
  border: 1px solid #5F9CF8;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .custom-input label input:hover:not(.focus-visible), .custom-input label input.focus-visible:focus {
    border-color: #5A7FFF;
  }
}
@media (max-width: 767px) {
  .custom-input label input {
    font-size: 20px;
  }
}
.custom-input label input::placeholder {
  font-size: inherit;
  line-height: inherit;
  color: rgba(0, 0, 0, 0.4);
}

.create-task-form {
  padding: 0;
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 0.3s ease, opacity 0.3s ease, padding-top 0.3s ease, padding-bottom 0.3s ease;
}
.create-task-form.is-active {
  padding-bottom: 30px;
  opacity: 1;
  max-height: 300px;
}