@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin has-hover {
  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover:not(.focus-visible),
    &.focus-visible:focus {
      @content;
    }
  }
}

// Desktop first

@mixin vp-767 {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

@mixin vp-1024 {
  @media (max-width: $vp-1024 - 1) {
    @content;
  }
}

@mixin reset-list {
  padding: 0;
  margin: 0;
  list-style: none;
}
